import {Component} from '@angular/core';
import {AlertController, IonicPage, NavController, NavParams} from 'ionic-angular';
import {CryptoParamUtilProvider} from "../../providers/crypto-param-util/crypto-param-util";
import {MaskTokenProvider} from "../../providers/mask-token/mask-token";
import {LocalStorageProvider} from "../../providers/local-storage/local-storage";
import {CashByCodeServiceProvider} from "../../providers/cash-by-code-service/cash-by-code-service";
import {TokenServiceProvider} from "../../providers/token-service/token-service";


@IonicPage()
@Component({
  selector: 'page-cash-by-code03',
  templateUrl: 'cash-by-code03.html',
})
export class CashByCode03Page {

  initCashByCodeResponse;
  srcAccDetails;
  cashByCodeError03;
  totalDeduction;
  formVal = {tpin: "", otp: ""};
  tpin;
  otp;
  nid;


  constructor(public navCtrl: NavController,
              public navParams: NavParams,
              public cryptoUtil: CryptoParamUtilProvider,
              public maskTokenUtil: MaskTokenProvider,
              public storage: LocalStorageProvider,
              public alertCtrl: AlertController,
              public tokenService: TokenServiceProvider,
              public cashByCodeService: CashByCodeServiceProvider) {

    this.initCashByCodeResponse = JSON.parse(this.navParams.get("initCashByCodeResponse"));
    this.nid = this.navParams.get("nid");
    this.srcAccDetails = JSON.parse(this.navParams.get("srcAccDetails"));

    this.totalDeduction = this.initCashByCodeResponse.transferAmount +
      this.initCashByCodeResponse.transferCharge +
      this.initCashByCodeResponse.vatOnTransferCharge;

  }

  CashByCode03Action = function () {
    let flag = true;
    this.tpin = this.formVal.tpin;
    this.otp = this.formVal.otp;


    let validateTpinExp = new RegExp("[0-9]{4}$");
    let validateOtpExp = new RegExp("[0-9]{6}$");


    if (this.tpin == "" || typeof(this.tpin) == "undefined") {
      this.cashByCodeError03 = "Please Enter TPIN.";
      flag = false;
      return;
    }

    if (this.otp == "" || typeof(this.otp) == "undefined") {
      this.cashByCodeError03 = "Please Enter OTP.";
      flag = false;
      return;
    }

    if (!validateTpinExp.test(this.tpin)) {
      this.cashByCodeError03 = "Invalid TPIN.";
      flag = false;
      this.formVal = {tpin: ""};
      return;
    }

    if (!validateOtpExp.test(this.otp)) {
      this.cashByCodeError03 = "Invalid OTP.";
      flag = false;
      this.formVal = {otp: ""};
      return;
    }

    if (flag) {
      let appData = this.storage.retrieveFromStorage("appData");
      let unMaskRefToken = this.maskTokenUtil.getUnMaskVal(appData.refreshToken);
      this.tokenService.getNewToken(unMaskRefToken)
        .then(data => {
          this.onNewTokenGenComplete(data);
        });
    }

  };

  onNewTokenGenComplete = function (data) {

    if (data.status == 0) {
      this.navCtrl.setRoot('ExpirePage');
    }

    else {
      let appData = this.storage.retrieveFromStorage("appData");
      let encTxID = this.cryptoUtil.getEncryptVal(this.initCashByCodeResponse.transferID);
      let encOtp = this.cryptoUtil.getEncryptVal(this.otp);
      let encTpin = this.cryptoUtil.getEncryptVal(this.tpin);
      let unMaskToken = this.maskTokenUtil.getUnMaskVal(appData.token);
      this.cashByCodeService.confirmCashRemittance(unMaskToken, appData.userId, appData.appId, encTxID, encTpin, encOtp)
        .then(data => {
          this.onConfirmCashByCodeComplete(data);
        });
    }
  };

  onConfirmCashByCodeComplete = function (data) {

    if (data.statusCode == 1) {
      this.navCtrl.push('CashByCode04Page', {
        'srcAccTitle': this.srcAccTitle,
        'initCashByCodeResponse': JSON.stringify(this.initCashByCodeResponse),
        'confirmCashByCodeResponse': JSON.stringify(data.object),
        'totalDeduction': this.totalDeduction
      });
    }

    else if (data.statusCode == 0) {
      this.cashByCodeError03 = data.note;
      this.formVal = {tpin: "", otp: ""};
    }

    else {
      this.cashByCodeError03 = "System Error Contact with Administrator.";
    }
  };

  ionViewDidLoad() {

  }

}



